/* tslint:disable */
/* eslint-disable */
import type { CompletionRequest } from '../models/CompletionRequest';
import type { CompletionResponse } from '../models/CompletionResponse';
import type { HealthState } from '../models/HealthState';
import type { LogEventRequest } from '../models/LogEventRequest';

import type { CancelablePromise } from '../core/CancelablePromise';
import type { BaseHttpRequest } from '../core/BaseHttpRequest';

export class V1Service {

  constructor(public readonly httpRequest: BaseHttpRequest) {}

  /**
   * @param requestBody
   * @returns CompletionResponse Success
   * @throws ApiError
   */
  public completion(
    requestBody: CompletionRequest,
  ): CancelablePromise<CompletionResponse> {
    return this.httpRequest.request({
      method: 'POST',
      url: '/v1/completions',
      body: requestBody,
      mediaType: 'application/json',
      errors: {
        400: `Bad Request`,
      },
    });
  }

  /**
   * @param requestBody
   * @returns any Success
   * @throws ApiError
   */
  public event(
    requestBody: LogEventRequest,
  ): CancelablePromise<any> {
    return this.httpRequest.request({
      method: 'POST',
      url: '/v1/events',
      body: requestBody,
      mediaType: 'application/json',
      errors: {
        400: `Bad Request`,
      },
    });
  }

  /**
   * @returns HealthState Success
   * @throws ApiError
   */
  public health(): CancelablePromise<HealthState> {
    return this.httpRequest.request({
      method: 'POST',
      url: '/v1/health',
    });
  }

}
